const getDefaultState = () => {
  return {
    locale: process.env.VUE_APP_LOCALE,
  };
};

const getters = {
  locale: (state) => state.locale,
};

const mutations = {
  SET_LOCALE(state, locale) {
    state.locale = locale;
  },
};

const actions = {
  setLocale({ commit }, locale) {
    commit("SET_LOCALE", locale);
  },
};

export default {
  state: getDefaultState(),
  getters,
  actions,
  mutations,
};
