<template>
  <v-app>
    <app-bar @toggle="toggleDrawer"></app-bar>
    <side-bar :expandDrawer="expandDrawer"></side-bar>
    <v-main class="grey lighten-3">
      <v-row>
        <v-col>
          <!--
            Even when routes use the same component, treat them
            as distinct and create the component again.
          -->
          <router-view :key="$route.fullPath" class="pa-4"></router-view>
        </v-col>
      </v-row>
    </v-main>
  </v-app>
</template>

<script>
import AppBar from "@/components/AppBar.vue";
import SideBar from "@/components/SideBar.vue";

export default {
  name: "MainLayout",
  components: { AppBar, SideBar },
  data() {
    return {
      expandDrawer: true,
    };
  },
  created() {
    if (this.$vuetify.breakpoint.mobile) this.expandDrawer = false;
  },
  methods: {
    toggleDrawer() {
      this.expandDrawer = !this.expandDrawer;
    },
  },
};
</script>
